import { Commit } from 'vuex';
import { isAuthorized as isAuth } from '../../Services/AuthService';

interface State {
  authorized: boolean
}

export default {
  namespaced: true,
  state: {
    authorized: false,
  },
  getters: {
    isAuthorized: (state: { authorized: boolean; }) => state.authorized,
  },
  mutations: {
    SET_AUTHORIZED(state: State, payload: boolean) {
      state.authorized = payload;
    },
  },
  actions: {
    async isAuthorized({ commit }: { commit: Commit }) {
      const response = await isAuth();

      return new Promise((resolve, reject) => {
        commit('SET_AUTHORIZED', response);

        resolve(response);
      });
    },
  },
};
